import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import Moment from 'react-moment';

class CompleteOrdersInMonth extends Component {
  state = {
    orders: []
  };

  componentDidMount() {
    if (this.props.order.completeOrdersInMonth && this.props.order.completeOrdersInMonth.length > 0) {
      this.setState({
        orders: this.props.order.completeOrdersInMonth
      });
    }
  }

  render() {
    let completeOrders = this.state.orders.map((order, index) => {

      let consumptionArray = [];
      order.disinfectors.forEach(item => {
        consumptionArray.push({
          user: item.user,
          consumption: item.consumption
        });
      });

      let consumptionRender = consumptionArray.map((item, index) =>
        <li key={index}>
          <p className="mb-0">: {item.user.occupation} {item.user.name}</p>
          {item.consumption.map((element, number) =>
            <p key={number} className="mb-0">{element.material}: {element.amount.toLocaleString()} {element.unit}</p>
          )}
        </li>
      );

      return (
        <div className="col-lg-4 col-md-6 mt-3" key={index}>
          <div className="card order">
            <div className="card-body p-0">
              <ul className="font-bold mb-0 list-unstyled">
                {order.prevFailedOrder && (
                  <li><h4><i className="fas fas fa-exclamation"></i>   <i className="fas fas fa-exclamation"></i></h4></li>
                )}

                {order.failed && (
                  <li><h4><i className="fas fas fa-exclamation"></i>   <i className="fas fas fa-exclamation"></i></h4></li>
                )}

                {order.operatorDecided ? (
                  <React.Fragment>
                    <li>  </li>
                    {order.operatorConfirmed ? (
                      <React.Fragment>
                        <li className="text-success">  (<Moment format="DD/MM/YYYY HH:mm">{order.operatorCheckedAt}</Moment>)</li>
                        <li> (0-5): {order.score}</li>
                        <li> : {order.clientReview ? order.clientReview : ' '}</li>
                      </React.Fragment>
                    ) : <li className="text-danger">  (<Moment format="DD/MM/YYYY HH:mm">{order.operatorCheckedAt}</Moment>)</li>}
                  </React.Fragment>
                ) : <li>    </li>}






                {order.accountantDecided ? (
                  <React.Fragment>
                    <li>  </li>
                    {order.accountantConfirmed ? (
                      <React.Fragment>
                        <li className="text-success">  (<Moment format="DD/MM/YYYY HH:mm">{order.accountantCheckedAt}</Moment>)</li>
                        <li>-: {order.invoice}</li>
                        <li> : {order.cost.toLocaleString()} UZS (  {(order.cost / order.disinfectors.length).toLocaleString()} UZS)</li>
                      </React.Fragment>
                    ) : <li className="text-danger">  (<Moment format="DD/MM/YYYY HH:mm">{order.accountantCheckedAt}</Moment>)</li>}
                  </React.Fragment>
                ) : (
                  <React.Fragment>

                    {order.adminDecided ? (
                      <React.Fragment>
                        <li>  </li>
                        {order.adminConfirmed ? (
                          <li className="text-success">  (<Moment format="DD/MM/YYYY HH:mm">{order.adminCheckedAt}</Moment>)</li>
                        ) : <li className="text-danger">  (<Moment format="DD/MM/YYYY HH:mm">{order.adminCheckedAt}</Moment>)</li>}
                      </React.Fragment>
                    ) : (
                      <li>    </li>
                    )}

                  </React.Fragment>
                )}



                {/* {order.clientType === 'corporate' && order.paymentMethod === 'notCash' && !order.accountantDecided ? <li>    </li> : ''}

                {order.clientType === 'corporate' && order.paymentMethod === 'notCash' && order.accountantDecided ?
                  <React.Fragment>
                    <li>  </li>
                    {order.accountantConfirmed ? (
                      <React.Fragment>
                        <li className="text-success">  (<Moment format="DD/MM/YYYY HH:mm">{order.accountantCheckedAt}</Moment>)</li>
                        <li>-: {order.invoice}</li>
                        <li> : {order.cost.toLocaleString()} UZS (  {(order.cost / order.disinfectors.length).toLocaleString()} UZS)</li>
                      </React.Fragment>
                    ) : <li className="text-danger">  (<Moment format="DD/MM/YYYY HH:mm">{order.accountantCheckedAt}</Moment>)</li>}
                  </React.Fragment>
                  : ''}

                {order.clientType === 'corporate' && order.paymentMethod === 'cash' && !order.adminDecided ? <li>    </li> : ''}

                {order.clientType === 'corporate' && order.paymentMethod === 'cash' && order.adminDecided ? (
                  <React.Fragment>
                    <li>  </li>
                    {order.adminConfirmed ? (
                      <li className="text-success">  (<Moment format="DD/MM/YYYY HH:mm">{order.adminCheckedAt}</Moment>)</li>
                    ) : <li className="text-danger">  (<Moment format="DD/MM/YYYY HH:mm">{order.adminCheckedAt}</Moment>)</li>}
                  </React.Fragment>
                ) : ''}

                {order.clientType === 'individual' && !order.adminDecided ? <li>    </li> : ''}
                {order.clientType === 'individual' && order.adminDecided ? (
                  <React.Fragment>
                    <li>  </li>
                    {order.adminConfirmed ? (
                      <li className="text-success">  (<Moment format="DD/MM/YYYY HH:mm">{order.adminCheckedAt}</Moment>)</li>
                    ) : <li className="text-danger">  (<Moment format="DD/MM/YYYY HH:mm">{order.adminCheckedAt}</Moment>)</li>}
                  </React.Fragment>
                ) : ''} */}






                {order.clientType === 'corporate' ?
                  <React.Fragment>
                    {order.clientId ? (
                      <li className="text-danger"> : {order.clientId.name}</li>
                    ) : <li className="text-danger"> </li>}
                    <li className="text-danger"> : {order.client}</li>
                  </React.Fragment>
                  : ''}

                {order.clientType === 'individual' ?
                  <li className="text-danger"> : {order.client}</li>
                  : ''}

                <li className="text-danger"> : {order.phone}</li>
                {order.phone2 ? <li className="text-danger"> : {order.phone2}</li> : ''}
                <li className="text-danger"> : <Moment format="DD/MM/YYYY">{order.dateFrom}</Moment></li>
                <li className="text-danger"> :  <Moment format="HH:mm">{order.dateFrom}</Moment>  <Moment format="HH:mm">{order.completedAt}</Moment></li>
                <li className="text-danger">: {order.address}</li>
                <li className="text-danger"> : {order.typeOfService}</li>

                <li> : {order.comment ? order.comment : '--'}</li>
                <li> : {order.disinfectorComment ? order.disinfectorComment : '--'}</li>

                <li>  (  {order.disinfectors.length} ):</li>
                <ul className="font-bold mb-0">
                  {consumptionRender}
                </ul>

                {order.clientType === 'corporate' ? (
                  <React.Fragment>
                    {order.paymentMethod === 'cash' ? (
                      <React.Fragment>
                        <li> : </li>
                        <li> : {order.cost.toLocaleString()} UZS (  {(order.cost / order.disinfectors.length).toLocaleString()} UZS)</li>
                      </React.Fragment>
                    ) : (
                      <React.Fragment>
                        <li> : </li>
                        <li> : {order.contractNumber}</li>
                      </React.Fragment>
                    )}
                  </React.Fragment>
                ) : ''}

                {order.completed && order.clientType === 'individual' && order.cost ?
                  <li> : {order.cost.toLocaleString()} UZS  (  {(order.cost / order.disinfectors.length).toLocaleString()} UZS)</li>
                  : ''}

                {/* {order.userAcceptedOrder ? (
                  <li> : {order.userAcceptedOrder.occupation} {order.userAcceptedOrder.name}</li>
                ) : ''} */}

                {/* {order.userCreated ? (
                  <li> : {order.userCreated.occupation} {order.userCreated.name}</li>
                ) : ''} */}

                <li>   : <Moment format="DD/MM/YYYY HH:mm">{order.completedAt}</Moment></li>
              </ul>
            </div>
          </div>
        </div>
      )
    });

    return (
      <div className="row">
        {this.props.searchMethod !== '' && this.state.orders.length === 0 ? (
          <div className="col-12">
            <h3 className="text-center"> </h3>
          </div>
        ) : (
          completeOrders
        )}
        {/* {completeOrders} */}
      </div>
    )
  }
}

const mapStateToProps = (state) => ({
  auth: state.auth,
  order: state.order,
  operator: state.operator,
  errors: state.errors
});

export default connect(mapStateToProps)(withRouter(CompleteOrdersInMonth));