import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import Moment from 'react-moment';

import { getQueriesForAdmin, adminConfirmsOrderQuery } from '../../actions/adminActions';

class ShowQueriesForAdmin extends Component {
  _isMounted = false;

  state = {
    orderQueries: []
  };

  componentDidMount() {
    this._isMounted = true;
    if (this.props.admin.orderQueries && this.props.admin.orderQueries.length > 0) {
      this.setState({
        orderQueries: this.props.admin.orderQueries
      });
    }
  }

  adminConfirmsOrderQuery = (orderId, response, disinfectors) => {
    const object = {
      orderId: orderId,
      response: response,
      disinfectors: disinfectors
    };
    this.props.adminConfirmsOrderQuery(object, this.props.history);
    window.location.reload();
  };

  goToAddNewForm = (order) => {
    this.props.history.push(`/fail/add-new/${order._id}`, {
      pathname: `/fail/add-new/${order._id}`,
      state: { order }
    });
    // this.props.goToAddNewForm(order, this.props.history);
  };

  componentWillUnmount() {
    this._isMounted = false;
  }

  render() {
    let orderQueries = this.state.orderQueries.map((order, index) => {

      let consumptionArray = [];
      order.disinfectors.forEach(item => {
        consumptionArray.push({
          user: item.user,
          consumption: item.consumption
        });
      });

      let renderConsumptionOfOrder = consumptionArray.map((item, index) => {
        return (
          <li key={index}>
            <p className="mb-0">: {item.user.occupation} {item.user.name}</p>
            {item.consumption.map((element, number) =>
              <p key={number} className="mb-0">{element.material}: {element.amount.toLocaleString()} {element.unit}</p>
            )}
          </li>
        );
      });

      return (
        <div className="col-lg-4 col-md-6 mt-3" key={index}>
          <div className="card order mt-2">
            <div className="card-body p-0">
              <ul className="font-bold mb-0 pl-0 list-unstyled">
                {order.prevFailedOrder && (
                  <li><h4><i className="fas fas fa-exclamation"></i>   <i className="fas fas fa-exclamation"></i></h4></li>
                )}

                {order.failed && (
                  <li><h4><i className="fas fas fa-exclamation"></i>  <i className="fas fas fa-exclamation"></i></h4></li>
                )}

                {order.returnedBack ? (
                  <li className="text-danger">  </li>
                ) : ''}

                {order.disinfectorId ? (
                  <li>: {order.disinfectorId.occupation} {order.disinfectorId.name}</li>
                ) : ''}

                {order.clientType === 'corporate' ?
                  <React.Fragment>
                    {order.clientId ? (
                      <li className="text-danger"> : {order.clientId.name}</li>
                    ) : <li className="text-danger"> </li>}
                    <li className="text-danger"> : {order.client}</li>
                  </React.Fragment>
                  : ''}

                {order.clientType === 'individual' ?
                  <li className="text-danger"> : {order.client}</li>
                  : ''}

                <li className="text-danger"> : {order.phone}</li>
                <li className="text-danger">: <Moment format="DD/MM/YYYY">{order.dateFrom}</Moment></li>
                <li className="text-danger"> :  <Moment format="HH:mm">{order.dateFrom}</Moment>  <Moment format="HH:mm">{order.completedAt}</Moment></li>
                <li className="text-danger">: {order.address}</li>
                <li> : {order.typeOfService}</li>
                <li> : {order.advertising}</li>
                <li>   : <Moment format="DD/MM/YYYY HH:mm">{order.completedAt}</Moment></li>
                <li>  ( ): {order.guarantee}</li>

                <li>  (  {order.disinfectors.length} ):</li>
                <ul className="font-bold mb-0">
                  {renderConsumptionOfOrder}
                </ul>

                {order.clientType === 'corporate' ? (
                  <React.Fragment>
                    {order.paymentMethod === 'cash' ? (
                      <React.Fragment>
                        <li> : </li>
                        <li> : {order.cost.toLocaleString()} UZS (  {(order.cost / order.disinfectors.length).toLocaleString()} UZS)</li>
                      </React.Fragment>
                    ) : (
                      <React.Fragment>
                        <li> : </li>
                        <li> : {order.contractNumber}</li>
                      </React.Fragment>
                    )}
                  </React.Fragment>
                ) : ''}

                {order.clientType === 'individual' ?
                  <li> : {order.cost.toLocaleString()}  (  {(order.cost / order.disinfectors.length).toFixed(2).toLocaleString()} )</li>
                  : ''}

                {order.userCreated ? (
                  <li> : {order.userCreated.occupation} {order.userCreated.name}</li>
                ) : ''}

                {order.userAcceptedOrder ? (
                  <li> : {order.userAcceptedOrder.occupation} {order.userAcceptedOrder.name}</li>
                ) : ''}

                {order.operatorDecided ? (
                  <React.Fragment>
                    <li>  </li>
                    {order.operatorConfirmed ? (
                      <React.Fragment>
                        <li className="text-success">  (<Moment format="DD/MM/YYYY HH:mm">{order.operatorCheckedAt}</Moment>)</li>
                        <li> (0-5): {order.score}</li>
                        <li> : {order.clientReview ? order.clientReview : ' '}</li>
                      </React.Fragment>
                    ) : <li className="text-danger">  (<Moment format="DD/MM/YYYY HH:mm">{order.operatorCheckedAt}</Moment>)</li>}
                  </React.Fragment>
                ) : <li>    </li>}
              </ul>

              <div className="btn-group mt-3">
                <button className="btn btn-danger mr-2" onClick={() => { if (window.confirm('   ?')) { this.adminConfirmsOrderQuery(order._id, 'false') } }}></button>
              </div>

              <div className="btn-group mt-3">
                <button className="btn btn-success mr-2" onClick={() => { if (window.confirm('   ?')) { this.adminConfirmsOrderQuery(order._id, 'true') } }}></button>
              </div>

              <div className="btn-group mt-3">
                <button className="btn btn-dark mr-2" onClick={() => { if (window.confirm('     ?')) { this.adminConfirmsOrderQuery(order._id, 'back', order.disinfectors) } }}></button>
              </div>

              {!order.failed ? (
                <div className="btn-group mt-3">
                  <button
                    className="btn btn-secondary mr-2"
                    onClick={() => this.goToAddNewForm(order)}
                  > </button>
                </div>
              ) : ''}

            </div>
          </div>
        </div>
      )
    });

    return (
      <React.Fragment>
        <div className="row">
          <div className="col-12">
            <h1 className="text-center"> </h1>
          </div>

          <div className="col-12">
            <button className="btn btn-primary" onClick={() => this.props.getQueriesForAdmin()}> </button>
          </div>
          {orderQueries}
        </div>
      </React.Fragment>
    )
  }
}

const mapStateToProps = (state) => ({
  auth: state.auth,
  order: state.order,
  admin: state.admin,
  errors: state.errors
});

export default connect(mapStateToProps, { getQueriesForAdmin, adminConfirmsOrderQuery })(withRouter(ShowQueriesForAdmin));