import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import { registerUser } from '../../actions/authActions';
import TextFieldGroup from '../common/TextFieldGroup';
import SelectListGroup from '../common/SelectListGroup';

class Register extends Component {
  constructor() {
    super();
    this.state = {
      name: '',
      email: '',
      phone: '',
      occupation: '',
      color: '',
      password: '',
      password2: '',
      errors: {}
    };
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.errors) {
      this.setState({ errors: nextProps.errors });
    }
  }

  onChange = (e) => this.setState({ [e.target.name]: e.target.value });

  onSubmit = (e) => {
    e.preventDefault();

    const newUser = {
      name: this.state.name,
      email: this.state.email,
      phone: this.state.phone,
      occupation: this.state.occupation,
      color: this.state.color,
      password: this.state.password,
      password2: this.state.password2
    };

    this.props.registerUser(newUser, this.props.history);
  }

  render() {
    const { errors } = this.state;

    // user occupation options
    const occupationOptions = [
      { label: '--    --', value: 0 },
      { label: '', value: 'admin' },
      { label: '', value: 'subadmin' },
      { label: '', value: 'operator' },
      { label: '', value: 'disinfector' },
      // { label: '', value: 'marketer' },
      { label: '', value: 'accountant' }
    ];

    const colorOptions = [
      { label: '--    ( ) --', value: 0 },
      { label: '', value: 'red' },
      { label: '', value: 'green' },
      { label: '', value: 'blue' },
      { label: '', value: 'orange' },
      { label: '', value: 'yellow' },
      { label: '', value: 'violet' },
      { label: '', value: 'brown' },
      { label: '', value: 'gold' },
      { label: '-', value: 'greenyellow' },
      { label: '', value: 'indigo' },
      { label: '-', value: 'lightsteelblue' },
      { label: '', value: 'orchid' },
      { label: '  ', value: 'seagreen' },
      { label: '', value: 'turquoise' },
      { label: '', value: 'bisque' },
      { label: '', value: 'cyan' },
      { label: 'DarkGoldenRod', value: 'darkgoldenrod' }
    ];

    return (
      <div className="container register mt-4">
        <div className="row">
          <div className="col-md-8 m-auto">
            <div className="card">
              <div className="card-body">
                <h2 className="text-center"> </h2>
                <form noValidate onSubmit={this.onSubmit}>
                  <TextFieldGroup
                    label=" "
                    type="text"
                    name="name"
                    value={this.state.name}
                    onChange={this.onChange}
                    error={errors.name}
                  />
                  <TextFieldGroup
                    label=" E-mail"
                    name="email"
                    type="email"
                    value={this.state.email}
                    onChange={this.onChange}
                    error={errors.email}
                  />
                  <TextFieldGroup
                    label="  "
                    type="phone"
                    name="phone"
                    value={this.state.phone}
                    onChange={this.onChange}
                    error={errors.phone}
                  />
                  <SelectListGroup
                    name="occupation"
                    value={this.state.occupation}
                    onChange={this.onChange}
                    error={errors.occupation}
                    options={occupationOptions}
                  />
                  {this.state.occupation === 'disinfector' ||
                    this.state.occupation === 'subadmin' ? (
                    <SelectListGroup
                      name="color"
                      value={this.state.color}
                      onChange={this.onChange}
                      error={errors.color}
                      options={colorOptions}
                    />
                  ) : ''}
                  <TextFieldGroup
                    label=" "
                    type="password"
                    name="password"
                    value={this.state.password}
                    onChange={this.onChange}
                    error={errors.password}
                  />
                  <TextFieldGroup
                    label=" "
                    type="password"
                    name="password2"
                    value={this.state.password2}
                    onChange={this.onChange}
                    error={errors.password2}
                  />
                  <button type="submit" className="btn btn-success btn-block mt-4"><i className="fas fa-user-plus"></i> </button>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  auth: state.auth,
  errors: state.errors
});

export default connect(mapStateToProps, { registerUser })(withRouter(Register));