import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import Spinner from '../common/Spinner';
import Moment from 'react-moment';
import TextFieldGroup from '../common/TextFieldGroup';
import TextAreaFieldGroup from '../common/TextAreaFieldGroup';

import ClientNotSatisfiedButton from '../common/ClientNotSatisfiedButton';
import { getCompleteOrderById, confirmCompleteOrder } from '../../actions/operatorActions';


class ConfirmOrder extends Component {
  state = {
    clientReview: '',
    score: '',
    errors: {}
  };

  componentDidMount() {
    this.props.getCompleteOrderById(this.props.match.params.id);
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.errors) {
      this.setState({ errors: nextProps.errors });
    }
  }

  onChange = (e) => this.setState({ [e.target.name]: e.target.value });

  onSubmit = (e) => {
    e.preventDefault();

    if (this.state.clientReview === '') {
      return alert('  ');
    }

    const object = {
      orderId: this.props.operator.orderToConfirm._id,
      decision: 'confirm',
      clientReview: this.state.clientReview,
      score: this.state.score
    };
    this.props.confirmCompleteOrder(object, this.props.history);
  }

  reject = () => {
    const object = {
      orderId: this.props.operator.orderToConfirm._id,
      decision: 'reject',
      clientReview: '',
      score: ''
    };
    this.props.confirmCompleteOrder(object, this.props.history);
  }

  // goToAddNewForm = (order) => {
  //   this.props.history.push(`/fail/add-new/${order._id}`, {
  //     pathname: `/fail/add-new/${order._id}`,
  //     state: { order }
  //   });
  // };

  render() {
    const completeOrder = this.props.operator.orderToConfirm;
    const { errors } = this.state;

    let consumptionArray = [];
    completeOrder.disinfectors.forEach(item => {
      consumptionArray.push({
        user: item.user,
        consumption: item.consumption
      });
    });

    let consumptionRender = consumptionArray.map((item, index) =>
      <li key={index}>
        <p className="mb-0">: {item.user.occupation} {item.user.name}</p>
        {item.consumption.map((element, number) =>
          <p key={number} className="mb-0">{element.material}: {element.amount.toLocaleString()} {element.unit}</p>
        )}
      </li>
    );

    return (
      <div className="container-fluid">
        <div className="row">

          <div className="col-lg-6 col-md-9 mx-auto">
            {this.props.operator.loadingCompleteOrders ? <Spinner /> : (
              <React.Fragment>
                <h3 className="text-center"> </h3>
                <div className="card order mt-2">
                  <div className="card-body p-0">
                    <ul className="font-bold mb-0 list-unstyled">
                      {completeOrder.prevFailedOrder && (
                        <li><h4><i className="fas fas fa-exclamation"></i>   <i className="fas fas fa-exclamation"></i></h4></li>
                      )}

                      {completeOrder.failed && (
                        <li><h4><i className="fas fas fa-exclamation"></i>   <i className="fas fas fa-exclamation"></i></h4></li>
                      )}

                      {completeOrder.disinfectorId && (
                        <li>: {completeOrder.disinfectorId.occupation} {completeOrder.disinfectorId.name}</li>
                      )}


                      {completeOrder.failed && completeOrder.nextOrdersAfterFailArray && (
                        <React.Fragment>
                          <li className="text-primary">   : {completeOrder.nextOrdersAfterFailArray.length}</li>

                          {completeOrder.nextOrdersAfterFailArray.length > 0 && (
                            <React.Fragment>
                              <li className="text-primary">  : <Moment format="DD/MM/YYYY HH:mm">{completeOrder.nextOrdersAfterFailArray[completeOrder.nextOrdersAfterFailArray.length - 1].dateFrom}</Moment></li>

                              <li className="text-primary">  : {completeOrder.nextOrdersAfterFailArray[completeOrder.nextOrdersAfterFailArray.length - 1].disinfectorId.occupation} {completeOrder.nextOrdersAfterFailArray[completeOrder.nextOrdersAfterFailArray.length - 1].disinfectorId.name}</li>
                            </React.Fragment>
                          )}
                        </React.Fragment>
                      )}









                      {completeOrder.accountantDecided ? (
                        <React.Fragment>
                          <li>  </li>
                          {completeOrder.accountantConfirmed ? (
                            <React.Fragment>
                              <li className="text-success">  (<Moment format="DD/MM/YYYY HH:mm">{completeOrder.accountantCheckedAt}</Moment>)</li>
                              <li>-: {completeOrder.invoice ? completeOrder.invoice : '--'}</li>
                              <li> : {completeOrder.cost.toLocaleString()} UZS (  {(completeOrder.cost / completeOrder.disinfectors.length).toLocaleString()} UZS)</li>
                            </React.Fragment>
                          ) : <li className="text-danger">  (<Moment format="DD/MM/YYYY HH:mm">{completeOrder.accountantCheckedAt}</Moment>)</li>}
                        </React.Fragment>
                      ) : (
                        <React.Fragment>

                          {completeOrder.adminDecided ? (
                            <React.Fragment>
                              <li>  </li>
                              {completeOrder.adminConfirmed ? (
                                <li className="text-success">  (<Moment format="DD/MM/YYYY HH:mm">{completeOrder.adminCheckedAt}</Moment>)</li>
                              ) : <li className="text-danger">  (<Moment format="DD/MM/YYYY HH:mm">{completeOrder.adminCheckedAt}</Moment>)</li>}
                            </React.Fragment>
                          ) : (
                            <li>    </li>
                          )}

                        </React.Fragment>
                      )}



                      {/* {completeOrder.clientType === 'corporate' && completeOrder.paymentMethod === 'notCash' && !completeOrder.accountantDecided ? <li>    </li> : ''}

                    {completeOrder.clientType === 'corporate' && completeOrder.paymentMethod === 'notCash' && completeOrder.accountantDecided ?
                      <React.Fragment>
                        <li>  </li>
                        {completeOrder.accountantConfirmed ? (
                          <React.Fragment>
                            <li className="text-success">  (<Moment format="DD/MM/YYYY HH:mm">{completeOrder.accountantCheckedAt}</Moment>)</li>
                            <li>-: {completeOrder.invoice}</li>
                            <li> : {completeOrder.cost.toLocaleString()} (  {(completeOrder.cost / completeOrder.disinfectors.length).toLocaleString()})</li>
                          </React.Fragment>
                        ) : <li className="text-danger">  (<Moment format="DD/MM/YYYY HH:mm">{completeOrder.accountantCheckedAt}</Moment>)</li>}
                      </React.Fragment>
                      : ''}

                    {completeOrder.clientType === 'corporate' && completeOrder.paymentMethod === 'cash' && !completeOrder.adminDecided ? <li>    </li> : ''}

                    {completeOrder.clientType === 'corporate' && completeOrder.paymentMethod === 'cash' && completeOrder.adminDecided ? (
                      <React.Fragment>
                        <li>  </li>
                        {completeOrder.adminConfirmed ? (
                          <li className="text-success">  (<Moment format="DD/MM/YYYY HH:mm">{completeOrder.adminCheckedAt}</Moment>)</li>
                        ) : <li className="text-danger">  (<Moment format="DD/MM/YYYY HH:mm">{completeOrder.adminCheckedAt}</Moment>)</li>}
                      </React.Fragment>
                    ) : ''} */}













                      {completeOrder.clientType === 'corporate' ?
                        <React.Fragment>
                          {completeOrder.clientId ? (
                            <li className="text-danger"> : {completeOrder.clientId.name}</li>
                          ) : <li className="text-danger"> </li>}
                          <li className="text-danger"> : {completeOrder.client}</li>
                        </React.Fragment>
                        : ''}

                      {completeOrder.clientType === 'individual' ?
                        <li className="text-danger"> : {completeOrder.client}</li>
                        : ''}
                      <li className="text-danger">: <Moment format="DD/MM/YYYY">{completeOrder.dateFrom}</Moment></li>
                      <li className="text-danger"> :  <Moment format="HH:mm">{completeOrder.dateFrom}</Moment>  <Moment format="HH:mm">{completeOrder.completedAt}</Moment></li>
                      <li className="text-danger">: {completeOrder.address}</li>

                      <li className="text-danger"> : {completeOrder.phone}</li>
                      {completeOrder.phone2 !== '' ? <li className="text-danger"> : {completeOrder.phone2}</li> : ''}
                      <li className="text-danger"> : {completeOrder.typeOfService}</li>
                      <li>  ( ): {completeOrder.guarantee}</li>

                      <li> : {completeOrder.comment ? completeOrder.comment : '--'}</li>
                      <li> : {completeOrder.disinfectorComment ? completeOrder.disinfectorComment : '--'}</li>

                      <li>  (  {completeOrder.disinfectors.length} ):</li>
                      <ul className="font-bold mb-0">
                        {consumptionRender}
                      </ul>

                      {completeOrder.clientType === 'corporate' ? (
                        <React.Fragment>
                          {completeOrder.paymentMethod === 'cash' ? (
                            <React.Fragment>
                              <li> : </li>
                              <li> : {completeOrder.cost.toLocaleString()} UZS (  {(completeOrder.cost / completeOrder.disinfectors.length).toLocaleString()} UZS)</li>
                            </React.Fragment>
                          ) : (
                            <React.Fragment>
                              <li> : </li>
                              <li> : {completeOrder.contractNumber || ''}</li>
                            </React.Fragment>
                          )}
                        </React.Fragment>
                      ) : ''}

                      {completeOrder.clientType === 'individual' ?
                        <li> : {completeOrder.cost.toLocaleString()} UZS (  {(completeOrder.cost / completeOrder.disinfectors.length).toLocaleString()} UZS)</li>
                        : ''}

                      {completeOrder.userAcceptedOrder ? (
                        <li> : {completeOrder.userAcceptedOrder.occupation} {completeOrder.userAcceptedOrder.name}</li>
                      ) : ''}

                      {completeOrder.userCreated ? (
                        <li> : {completeOrder.userCreated.occupation} {completeOrder.userCreated.name}</li>
                      ) : ''}

                      <li>   : <Moment format="DD/MM/YYYY HH:mm">{completeOrder.completedAt}</Moment></li>
                    </ul>

                    <div className="btn-group mt-3">
                      <button className="btn btn-danger mr-2" onClick={() => { if (window.confirm('   ?')) { this.reject() } }}><i className="fas fa-ban"></i>   </button>
                    </div>

                    {/* {!completeOrder.failed ? (
                      <div className="btn-group mt-3">
                        <button
                          className="btn btn-secondary mr-2"
                          onClick={() => this.goToAddNewForm(completeOrder)}
                        > </button>
                      </div>
                    ) : ''} */}

                    <ClientNotSatisfiedButton
                      order={completeOrder}
                      shouldLoadOrder={true}
                    />
                  </div>
                </div>
              </React.Fragment>
            )}
          </div>


          <div className="col-lg-6 col-md-9 mx-auto">
            <div className="card mt-3 mb-3">
              <div className="card-body p-2">
                <h3 className="text-center">  </h3>
                {/* <form noValidate onSubmit={this.onSubmit}> */}
                <form onSubmit={this.onSubmit}>

                  <TextFieldGroup
                    label="     (0-5):"
                    type="number"
                    name="score"
                    min="0"
                    max="5"
                    value={this.state.score}
                    onChange={this.onChange}
                    error={errors.score}
                    required={true}
                  />

                  <TextAreaFieldGroup
                    name="clientReview"
                    placeholder=" "
                    value={this.state.clientReview}
                    onChange={this.onChange}
                    error={errors.clientReview}
                  />

                  <button className="btn btn-success btn-block">
                    <i className="fas fa-check-square"></i>   
                  </button>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    )
  }
}

const mapStateToProps = (state) => ({
  auth: state.auth,
  order: state.order,
  operator: state.operator,
  errors: state.errors
});

export default connect(mapStateToProps, { getCompleteOrderById, confirmCompleteOrder })(withRouter(ConfirmOrder));