import React, { Component } from 'react';
import { withRouter, Link } from 'react-router-dom';
import { connect } from 'react-redux';
import Spinner from '../common/Spinner';
import Moment from 'react-moment';

import { getReturnedQueries } from '../../actions/disinfectorActions';

class ReturnedQueries extends Component {
  state = {
    queries: [],
    loading: false
  };

  componentDidMount() {
    this.props.getReturnedQueries(this.props.auth.user.id);
    this.setState({
      loading: true
    });
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.disinfector.queries) {
      this.setState({
        queries: nextProps.disinfector.queries,
        loading: false
      });
    }
  }

  render() {
    let renderQueries = this.state.queries.map((order, index) =>
      <div className="col-lg-4 col-md-6 mt-3" key={index}>
        <div className="card order">
          <div className="card-body p-0">
            <ul className="font-bold mb-0 list-unstyled">

              {order.operatorDecided ? (
                <React.Fragment>
                  <li>  </li>
                  {order.operatorConfirmed ? (
                    <React.Fragment>
                      <li className="text-success">  (<Moment format="DD/MM/YYYY HH:mm">{order.operatorCheckedAt}</Moment>)</li>
                      <li> (0-5): {order.score}</li>
                      <li> : {order.clientReview ? order.clientReview : ' '}</li>
                    </React.Fragment>
                  ) : <li className="text-danger">  (<Moment format="DD/MM/YYYY HH:mm">{order.operatorCheckedAt}</Moment>)</li>}
                </React.Fragment>
              ) : <li>    </li>}






              {order.accountantDecided ? (
                <React.Fragment>
                  <li>  </li>
                  {order.accountantConfirmed ? (
                    <React.Fragment>
                      <li className="text-success">  (<Moment format="DD/MM/YYYY HH:mm">{order.accountantCheckedAt}</Moment>)</li>
                      <li>-: {order.invoice ? order.invoice : '--'}</li>
                      <li> : {order.cost.toLocaleString()} UZS (  {(order.cost / order.disinfectors.length).toLocaleString()} UZS)</li>
                    </React.Fragment>
                  ) : <li className="text-danger">  (<Moment format="DD/MM/YYYY HH:mm">{order.accountantCheckedAt}</Moment>)</li>}
                </React.Fragment>
              ) : (
                <React.Fragment>

                  {order.adminDecided ? (
                    <React.Fragment>
                      <li>  </li>
                      {order.adminConfirmed ? (
                        <li className="text-success">  (<Moment format="DD/MM/YYYY HH:mm">{order.adminCheckedAt}</Moment>)</li>
                      ) : <li className="text-danger">  (<Moment format="DD/MM/YYYY HH:mm">{order.adminCheckedAt}</Moment>)</li>}
                    </React.Fragment>
                  ) : (
                    <li>    </li>
                  )}

                </React.Fragment>
              )}


              {/* {order.clientType === 'corporate' && !order.accountantDecided ? <li>    </li> : ''}

              {order.clientType === 'corporate' && order.accountantDecided ?
                <React.Fragment>
                  <li>  </li>
                  {order.accountantConfirmed ? (
                    <React.Fragment>
                      <li className="text-success">  (<Moment format="DD/MM/YYYY HH:mm">{order.accountantCheckedAt}</Moment>)</li>
                      <li>-: {order.invoice}</li>
                      <li> : {order.cost.toLocaleString()} UZS (  {(order.cost / order.disinfectors.length).toLocaleString()} UZS)</li>
                    </React.Fragment>
                  ) : <li className="text-danger">  (<Moment format="DD/MM/YYYY HH:mm">{order.accountantCheckedAt}</Moment>)</li>}
                </React.Fragment>
                : ''}

              {order.clientType === 'individual' && !order.adminDecided ? <li>    </li> : ''}
              {order.clientType === 'individual' && order.adminDecided ? (
                <React.Fragment>
                  <li>  </li>
                  {order.adminConfirmed ? (
                    <li className="text-success">  (<Moment format="DD/MM/YYYY HH:mm">{order.adminCheckedAt}</Moment>)</li>
                  ) : <li className="text-danger">  (<Moment format="DD/MM/YYYY HH:mm">{order.adminCheckedAt}</Moment>)</li>}
                </React.Fragment>
              ) : ''} */}






              {order.clientType === 'corporate' ?
                <React.Fragment>
                  {order.clientId ? (
                    <li className="text-danger"> : {order.clientId.name}</li>
                  ) : <li className="text-danger"> </li>}
                  <li className="text-danger"> : {order.client}</li>
                </React.Fragment>
                : ''}

              {order.clientType === 'individual' ?
                <li className="text-danger"> : {order.client}</li>
                : ''}

              <li className="text-danger"> : {order.phone}</li>
              {order.phone2 ? <li className="text-danger"> : {order.phone2}</li> : ''}
              <li className="text-danger"> : <Moment format="DD/MM/YYYY">{order.dateFrom}</Moment></li>
              <li className="text-danger"> :  <Moment format="HH:mm">{order.dateFrom}</Moment>  <Moment format="HH:mm">{order.completedAt}</Moment></li>
              <li className="text-danger">: {order.address}</li>
              <li className="text-danger"> : {order.typeOfService}</li>

              <li> : {order.comment ? order.comment : '--'}</li>
              <li> : {order.disinfectorComment ? order.disinfectorComment : '--'}</li>

              {order.clientType === 'corporate' ? (
                <React.Fragment>
                  {order.paymentMethod === 'cash' ? (
                    <React.Fragment>
                      <li> : </li>
                      <li> : {order.cost.toLocaleString()} UZS (  {(order.cost / order.disinfectors.length).toLocaleString()} UZS)</li>
                    </React.Fragment>
                  ) : (
                    <React.Fragment>
                      <li> : </li>
                      <li> : {order.contractNumber}</li>
                    </React.Fragment>
                  )}
                </React.Fragment>
              ) : ''}

              {order.clientType === 'individual' ?
                <li> : {order.cost.toLocaleString()} UZS  (  {(order.cost / order.disinfectors.length).toLocaleString()} UZS)</li>
                : ''}

              <li>   : <Moment format="DD/MM/YYYY HH:mm">{order.completedAt}</Moment></li>

              {/* 21.04.2021 disinfector and subadmin now should have the same order complete form - OrderComplete component */}
              <Link
                to={`/order-complete-form/${order._id}`}
                className="btn btn-primary"
              >
                <i className="fab fa-wpforms"></i>    
              </Link>



              {/* {this.props.auth.user.occupation === 'subadmin' ? (
                <Link
                  to={`/subadmin/order-complete-form/${order._id}`}
                  className="btn btn-primary"
                >
                     
                </Link>
              ) : ''} */}

              {/* {this.props.auth.user.occupation === 'disinfector' ? (
              <Link
                to={`/order-complete-form/${order._id}`}
                className="btn btn-primary"
              >
                   
                </Link>
               ) : ''} */}
            </ul>
          </div>
        </div>
      </div>
    );

    return (
      <div className="container-fluid">
        <div className="row">
          <div className="col-12">
            <h3 className="text-center pl-3 pr-3"> ,         </h3>
          </div>
        </div>

        <div className="row">
          {this.state.loading ? (
            <div className="col-12">
              <Spinner />
            </div>
          ) : (
            <React.Fragment>
              {this.state.queries.length === 0 ? (
                <div className="col-12">
                  <h3 className="text-center pl-3 pr-3"> </h3>
                </div>
              ) : renderQueries}
            </React.Fragment>
          )}
        </div>
      </div>
    )
  }
}

const mapStateToProps = (state) => ({
  auth: state.auth,
  order: state.order,
  disinfector: state.disinfector,
  errors: state.errors
});

export default connect(mapStateToProps, { getReturnedQueries })(withRouter(ReturnedQueries));