import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import Spinner from '../common/Spinner';
import Moment from 'react-moment';
import { getOrderById } from '../../actions/orderActions';

class OrderDetails extends Component {
  componentDidMount() {
    this.props.getOrderById(this.props.match.params.id);
  }

  render() {
    const order = this.props.order.orderById;

    return (
      <div className="container">
        {this.props.order.loading ? <Spinner /> : (
          <div className="row">
            <div className="col-12">
              <h2 className="text-center"> </h2>
            </div>
            <div className="col-lg-8 col-md-10 m-auto">
              <div className="card order mt-2">
                <div className="card-body p-0">
                  <ul className="font-bold list-unstyled">
                    {order.prevFailedOrder && <h3><i className="fas fas fa-exclamation"></i>   <i className="fas fas fa-exclamation"></i></h3>}

                    {order.failed && <h3><i className="fas fas fa-exclamation"></i>   <i className="fas fas fa-exclamation"></i></h3>}

                    {order.disinfectorId && (
                      <li>: {order.disinfectorId.occupation} {order.disinfectorId.name}</li>
                    )}


                    {order.operatorDecided ? (
                      <React.Fragment>
                        <li>  </li>
                        {order.operatorConfirmed ? (
                          <React.Fragment>
                            <li className="text-success">  (<Moment format="DD/MM/YYYY HH:mm">{order.operatorCheckedAt}</Moment>)</li>
                            <li> (0-5): {order.score}</li>
                            <li> : {order.clientReview ? order.clientReview : ' '}</li>
                          </React.Fragment>
                        ) : <li className="text-danger">  (<Moment format="DD/MM/YYYY HH:mm">{order.operatorCheckedAt}</Moment>)</li>}
                      </React.Fragment>
                    ) : <li>    </li>}

                    {order.accountantDecided ? (
                      <React.Fragment>
                        <li>  </li>
                        {order.accountantConfirmed ? (
                          <React.Fragment>
                            <li className="text-success">  (<Moment format="DD/MM/YYYY HH:mm">{order.accountantCheckedAt}</Moment>)</li>
                            <li>-: {order.invoice ? order.invoice : '--'}</li>
                            <li> : {order.cost.toLocaleString()} UZS (  {(order.cost / order.disinfectors.length).toLocaleString()} UZS)</li>
                          </React.Fragment>
                        ) : <li className="text-danger">  (<Moment format="DD/MM/YYYY HH:mm">{order.accountantCheckedAt}</Moment>)</li>}
                      </React.Fragment>
                    ) : (
                      <React.Fragment>

                        {order.adminDecided ? (
                          <React.Fragment>
                            <li>  </li>
                            {order.adminConfirmed ? (
                              <li className="text-success">  (<Moment format="DD/MM/YYYY HH:mm">{order.adminCheckedAt}</Moment>)</li>
                            ) : <li className="text-danger">  (<Moment format="DD/MM/YYYY HH:mm">{order.adminCheckedAt}</Moment>)</li>}
                          </React.Fragment>
                        ) : (
                          <li>    </li>
                        )}

                      </React.Fragment>
                    )}


                    {order.clientType === 'corporate' ?
                      <React.Fragment>
                        {order.clientId ? (
                          <li className="text-danger"> : {order.clientId.name}</li>
                        ) : <li className="text-danger"> </li>}
                        <li className="text-danger"> : {order.client}</li>
                      </React.Fragment>
                      : ''}

                    {order.clientType === 'individual' ?
                      <li className="text-danger"> : {order.client}</li>
                      : ''}

                    <li className="text-danger"> : {order.phone}</li>
                    {order.phone2 !== '' ? (<li> : {order.phone2}</li>) : ''}
                    <li className="text-danger">: {order.address}</li>
                    <li className="text-danger"> : {order.typeOfService}</li>

                    {/* <li>: <Moment format="DD/MM/YYYY">{order.dateFrom}</Moment></li>
                    <li> :  <Moment format="HH:mm">{order.dateFrom}</Moment></li> */}

                    {order.dateFrom ? (
                      <React.Fragment>
                        <li>: <Moment format="DD/MM/YYYY">{order.dateFrom}</Moment></li>
                        {order.completed ? (
                          <li> :  <Moment format="HH:mm">{order.dateFrom}</Moment>  <Moment format="HH:mm">{order.completedAt}</Moment></li>
                        ) : <li> : <Moment format="HH:mm">{order.dateFrom}</Moment></li>}
                      </React.Fragment>
                    ) : <li>   : --</li>}


                    <li> : {order.comment ? order.comment : '--'}</li>
                    <li> : {order.disinfectorComment ? order.disinfectorComment : '--'}</li>

                    {order.userAcceptedOrder && (
                      <li> : {order.userAcceptedOrder.occupation} {order.userAcceptedOrder.name}</li>
                    )}

                    {order.userCreated && (
                      <li>  : {order.userCreated.occupation} {order.userCreated.name}</li>
                    )}

                    <li> : <Moment format="DD/MM/YYYY HH:mm">{order.createdAt}</Moment></li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        )}
      </div>
    )
  }
}

const mapStateToProps = (state) => ({
  auth: state.auth,
  order: state.order,
  operator: state.operator,
  errors: state.errors
});

export default connect(mapStateToProps, { getOrderById })(withRouter(OrderDetails));