import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import Moment from 'react-moment';

import ClientNotSatisfiedButton from '../common/ClientNotSatisfiedButton';

import removeZeros from '../../utils/removeZerosMat';
import calcMaterialConsumption from '../../utils/calcMatConsumption';
// import materials from '../common/materials';
// import guaranteeExpired from '../../utils/guaranteeExpired';


class ShowFails extends Component {
  state = {
    //    
    fails: []
  };

  componentDidMount() {
    if (this.props.fail.fails && this.props.fail.fails.length > 0) {
      this.setState({
        fails: this.props.fail.fails
      });
    }
  }

  // goToAddNewForm = (item) => {
  //   this.props.history.push(`/fail/add-new/${item._id}`, {
  //     pathname: `/fail/add-new/${item._id}`,
  //     state: { order: item, shouldLoadOrder: true }
  //   });
  // };

  render() {
    // let totalConsumption = [];
    // materials.forEach(object => {
    //   const emptyObject = {
    //     material: object.material,
    //     amount: 0,
    //     unit: object.unit
    //   };
    //   totalConsumption.push(emptyObject);
    // });

    // this.state.fails.forEach(order => {
    //   // calculate total consumption of all orders in given period
    //   order.disinfectors.forEach(element => {
    //     element.consumption.forEach(object => {
    //       totalConsumption.forEach(item => {
    //         if (object.material === item.material && object.unit === item.unit) {
    //           item.amount += object.amount;
    //         }
    //       });
    //     });
    //   });
    // });

    // calculate total consumption of failed and repeated orders in given period
    let totalConsumption = calcMaterialConsumption(this.state.fails);

    // remove materials with amount of 0
    totalConsumption = removeZeros(totalConsumption);

    let renderTotalConsumption = totalConsumption.map((item, key) =>
      <li key={key}>{item.material}: {item.amount.toLocaleString()} {item.unit}</li>
    );


    let failedCount = 0;
    let povtorsCount = 0;

    let renderFailedOrders = this.state.fails.map((order, key) => {
      if (order.failed) failedCount++;

      if (order.hasOwnProperty('prevFailedOrder')) povtorsCount++;


      // consumption array of specific confirmed order
      let consumptionArray = [];

      order.disinfectors.forEach(item => {
        consumptionArray.push({
          user: item.user,
          consumption: item.consumption
        });
      });

      let renderOrderConsumption = consumptionArray.map((object, number) =>
        <li key={number}>
          <p className="mb-0">: {object.user.occupation} {object.user.name}</p>
          {object.consumption.map((element, number) =>
            <p key={number} className="mb-0">{element.material}: {element.amount.toLocaleString()} {element.unit}</p>
          )}
        </li>
      );

      return (
        <div className="col-lg-4 col-md-6" key={key}>
          <div className="card order mt-2">
            <div className="card-body p-0">
              <ul className="font-bold mb-0 list-unstyled">
                {order.prevFailedOrder && (
                  <li><h4><i className="fas fas fa-exclamation"></i>   <i className="fas fas fa-exclamation"></i></h4></li>
                )}

                {order.failed && (
                  <li><h4><i className="fas fas fa-exclamation"></i>   <i className="fas fas fa-exclamation"></i></h4></li>
                )}

                {order.disinfectorId && (
                  <li>: {order.disinfectorId.occupation} {order.disinfectorId.name}</li>
                )}


                {order.failed && order.nextOrdersAfterFailArray && (
                  <React.Fragment>
                    <li className="text-primary">   : {order.nextOrdersAfterFailArray.length}</li>

                    {order.nextOrdersAfterFailArray.length > 0 && (
                      <React.Fragment>
                        <li className="text-primary">  : <Moment format="DD/MM/YYYY HH:mm">{order.nextOrdersAfterFailArray[order.nextOrdersAfterFailArray.length - 1].dateFrom}</Moment></li>

                        <li className="text-primary">  : {order.nextOrdersAfterFailArray[order.nextOrdersAfterFailArray.length - 1].disinfectorId.occupation} {order.nextOrdersAfterFailArray[order.nextOrdersAfterFailArray.length - 1].disinfectorId.name}</li>
                      </React.Fragment>
                    )}
                  </React.Fragment>
                )}

                {/* {order.nextOrdersAfterFailArray && order.nextOrdersAfterFailArray.length > 0 && (
                  <React.Fragment>
                    <li className="text-primary">  : <Moment format="DD/MM/YYYY HH:mm">{order.nextOrdersAfterFailArray[order.nextOrdersAfterFailArray.length - 1].dateFrom}</Moment></li>

                    <li className="text-primary">  : {order.nextOrdersAfterFailArray[order.nextOrdersAfterFailArray.length - 1].disinfectorId.occupation} {order.nextOrdersAfterFailArray[order.nextOrdersAfterFailArray.length - 1].disinfectorId.name}</li>
                  </React.Fragment>
                )} */}


                {order.prevFailedOrder && order.prevFailedOrder.disinfectorId && (
                  <li className="text-primary">  : <Moment format="DD/MM/YYYY HH:mm">{order.prevFailedOrder.dateFrom}</Moment> ({order.prevFailedOrder.disinfectorId.occupation} {order.prevFailedOrder.disinfectorId.name})</li>
                )}

                {/* {order.nextOrderAfterFail && order.nextOrderAfterFail.dateFrom && (
                  <li className="text-primary">  : <Moment format="DD/MM/YYYY HH:mm">{order.nextOrderAfterFail.dateFrom}</Moment></li>
                )}

                {order.nextOrderAfterFail && order.nextOrderAfterFail.disinfectorId && (
                  <li className="text-primary">  : {order.nextOrderAfterFail.disinfectorId.occupation} {order.nextOrderAfterFail.disinfectorId.name}</li>
                )} */}

                {order.operatorDecided ? (
                  <React.Fragment>
                    <li>  </li>
                    {order.operatorConfirmed ? (
                      <React.Fragment>
                        <li className="text-success">  (<Moment format="DD/MM/YYYY HH:mm">{order.operatorCheckedAt}</Moment>)</li>
                        <li> (0-5): {order.score}</li>
                      </React.Fragment>
                    ) : <li className="text-danger">  (<Moment format="DD/MM/YYYY HH:mm">{order.operatorCheckedAt}</Moment>)</li>}
                  </React.Fragment>
                ) : <li>    </li>}










                {order.accountantDecided ? (
                  <React.Fragment>
                    <li>  </li>
                    {order.accountantConfirmed ? (
                      <React.Fragment>
                        <li className="text-success">  (<Moment format="DD/MM/YYYY HH:mm">{order.accountantCheckedAt}</Moment>)</li>
                        <li>-: {order.invoice ? order.invoice : '--'}</li>
                        <li> : {order.cost.toLocaleString()} UZS (  {(order.cost / order.disinfectors.length).toLocaleString()} UZS)</li>
                      </React.Fragment>
                    ) : <li className="text-danger">  (<Moment format="DD/MM/YYYY HH:mm">{order.accountantCheckedAt}</Moment>)</li>}
                  </React.Fragment>
                ) : (
                  <React.Fragment>

                    {order.adminDecided ? (
                      <React.Fragment>
                        <li>  </li>
                        {order.adminConfirmed ? (
                          <li className="text-success">  (<Moment format="DD/MM/YYYY HH:mm">{order.adminCheckedAt}</Moment>)</li>
                        ) : <li className="text-danger">  (<Moment format="DD/MM/YYYY HH:mm">{order.adminCheckedAt}</Moment>)</li>}
                      </React.Fragment>
                    ) : (
                      <li>    </li>
                    )}

                  </React.Fragment>
                )}






                {/* {order.clientType === 'corporate' && order.paymentMethod === 'notCash' && !order.accountantDecided ? <li>    </li> : ''}

                {order.clientType === 'corporate' && order.paymentMethod === 'notCash' && order.accountantDecided ?
                  <React.Fragment>
                    <li>  </li>
                    {order.accountantConfirmed ? (
                      <React.Fragment>
                        <li className="text-success">  (<Moment format="DD/MM/YYYY HH:mm">{order.accountantCheckedAt}</Moment>)</li>
                        <li>-: {order.invoice}</li>
                        <li> : {order.cost.toLocaleString()} UZS (  {(order.cost / order.disinfectors.length).toLocaleString()} UZS)</li>
                      </React.Fragment>
                    ) : <li className="text-danger">  (<Moment format="DD/MM/YYYY HH:mm">{order.accountantCheckedAt}</Moment>)</li>}
                  </React.Fragment>
                  : ''}

                {order.clientType === 'corporate' && order.paymentMethod === 'cash' && !order.adminDecided ? <li>    </li> : ''}

                {order.clientType === 'corporate' && order.paymentMethod === 'cash' && order.adminDecided ? (
                  <React.Fragment>
                    <li>  </li>
                    {order.adminConfirmed ? (
                      <li className="text-success">  (<Moment format="DD/MM/YYYY HH:mm">{order.adminCheckedAt}</Moment>)</li>
                    ) : <li className="text-danger">  (<Moment format="DD/MM/YYYY HH:mm">{order.adminCheckedAt}</Moment>)</li>}
                  </React.Fragment>
                ) : ''}

                {order.clientType === 'individual' && !order.adminDecided ? <li>    </li> : ''}
                {order.clientType === 'individual' && order.adminDecided ? (
                  <React.Fragment>
                    <li>  </li>
                    {order.adminConfirmed ? (
                      <li className="text-success">  (<Moment format="DD/MM/YYYY HH:mm">{order.adminCheckedAt}</Moment>)</li>
                    ) : <li className="text-danger">  (<Moment format="DD/MM/YYYY HH:mm">{order.adminCheckedAt}</Moment>)</li>}
                  </React.Fragment>
                ) : ''} */}









                {order.clientType === 'corporate' ?
                  <React.Fragment>
                    {order.clientId ? (
                      <li className="text-danger"> : {order.clientId.name}</li>
                    ) : <li className="text-danger"> </li>}
                    <li className="text-danger"> : {order.client}</li>
                  </React.Fragment>
                  : ''}

                {order.clientType === 'individual' ?
                  <li className="text-danger"> : {order.client}</li>
                  : ''}

                <li className="text-danger"> : {order.phone}</li>
                <li className="text-danger"> : <Moment format="DD/MM/YYYY">{order.dateFrom}</Moment></li>
                <li className="text-danger"> :  <Moment format="HH:mm">{order.dateFrom}</Moment>  <Moment format="HH:mm">{order.completedAt}</Moment></li>
                <li className="text-danger">: {order.address}</li>
                <li className="text-danger"> : {order.typeOfService}</li>

                <li> : {order.comment ? order.comment : '--'}</li>
                <li> : {order.disinfectorComment ? order.disinfectorComment : '--'}</li>
                <li>  ( ): {order.guarantee}</li>

                <li>  (  {order.disinfectors.length} ):</li>
                <ul className="font-bold mb-0">
                  {renderOrderConsumption}
                </ul>

                {order.clientType === 'corporate' ? (
                  <React.Fragment>
                    {order.paymentMethod === 'cash' ? (
                      <React.Fragment>
                        <li> : </li>
                        <li> : {order.cost.toLocaleString()} UZS (  {(order.cost / order.disinfectors.length).toLocaleString()} UZS)</li>
                      </React.Fragment>
                    ) : (
                      <React.Fragment>
                        <li> : </li>
                        <li> : {order.contractNumber || '--'}</li>
                      </React.Fragment>
                    )}
                  </React.Fragment>
                ) : ''}

                {order.clientType === 'individual' ?
                  <li> : {order.cost.toLocaleString()} UZS (  {(order.cost / order.disinfectors.length).toLocaleString()} UZS)</li>
                  : ''}

                {/* show   button */}
                {/* {['admin', 'accountant', 'subadmin', 'operator'].includes(this.props.auth.user.occupation) &&
                  order.completed && !order.failed && order.guarantee &&
                  !guaranteeExpired(order.completedAt, order.guarantee) ? (
                  <button
                    className="btn btn-secondary mt-2 mr-2"
                    onClick={() => this.goToAddNewForm(order)}
                  > </button>
                ) : ''} */}

                <ClientNotSatisfiedButton
                  order={order}
                  shouldLoadOrder={true}
                />

              </ul>
            </div>
          </div>
        </div>
      );
    });

    return (
      <React.Fragment>
        <div className="row">
          <div className="col-lg-4 col-md-6">
            <div className="card order mt-2">
              <div className="card-body p-0">
                <ul className="font-bold mb-0 list-unstyled">
                  <li>  : {failedCount}</li>
                  <li>  : {povtorsCount}</li>
                </ul>
              </div>
            </div>
          </div>

          <div className="col-lg-4 col-md-6">
            <div className="card order mt-2">
              <div className="card-body p-0">
                <h4 className="text-center">     </h4>
                <ul className="font-bold mb-0 list-unstyled">
                  {renderTotalConsumption}
                </ul>
              </div>
            </div>
          </div>
        </div>

        {this.state.fails.length > 0 && (
          <div className="row mt-2">
            <div className="col-12">
              <h2 className="text-center pl-3 pr-3">   </h2>
            </div>
            {renderFailedOrders}
          </div>
        )}
      </React.Fragment>
    )
  }
}

const mapStateToProps = (state) => ({
  auth: state.auth,
  order: state.order,
  admin: state.admin,
  fail: state.fail,
  errors: state.errors
});

export default connect(mapStateToProps)(withRouter(ShowFails));