import React, { Component } from 'react';
import { withRouter, Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { logoutUser } from '../../actions/authActions';
import { clearCurrentProfile } from '../../actions/profileActions';
import { setTheme } from '../../actions/optionsActions';

class Navbar extends Component {
  onLogoutClick = (e) => {
    e.preventDefault();
    this.props.clearCurrentProfile();
    this.props.logoutUser(this.props.history);
  }

  changeTheme = (theme) => {
    this.props.setTheme(theme);
  };


  // fullRefresh = () => {
  //   window.location.reload(true);
  // };

  render() {
    const { isAuthenticated, user } = this.props.auth;

    // const showRefreshButtton = (
    //   <li className="nav-item">
    //     <button className="btn btn-grey nav-link" onClick={() => this.fullRefresh()}> Pro Team</button>
    //   </li>
    // );

    const authLinks = (
      <div className="authLinks">
        {/* <div className="dropdown">
          <button type="button" className="btn btn-primary dropdown-toggle align-baseline mr-2" data-toggle="dropdown"></button>
          <div className="dropdown-menu">
            <li className="nav-item">
              <Link to="/chat" className="nav-link"></Link>
            </li>
            <li className="nav-item">
              <Link to="/anons" className="nav-link"></Link>
            </li>
          </div>
        </div> */}


        <li className="nav-item list-inline-item">
          <Link to="/login" onClick={this.onLogoutClick} className="nav-link logout">
            <i className="fas fa-sign-out-alt"></i> Logout
          </Link>
        </li>
      </div>
    );


    const guestLinks = (
      <div className="guestLinks">
        <li className="nav-item list-inline-item">
          <Link className="nav-link" to="/login">
            Login
          </Link>
        </li>
      </div>
    );


    const disinfectorLinks = (
      <div className="disinfectorLinks">

        <div className="dropdown">
          <button type="button" className="btn btn-primary dropdown-toggle align-baseline mr-2" data-toggle="dropdown"></button>
          <div className="dropdown-menu">
            <li className="nav-item">
              <Link className="nav-link" to="/disinfector/stats"></Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/disinfector/queries"></Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/returned-queries"> </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/fail/see">.  . </Link>
            </li>
            {/* {showRefreshButtton} */}
          </div>
        </div>

        <div className="dropdown">
          <button type="button" className="btn btn-primary dropdown-toggle align-baseline mr-2" data-toggle="dropdown"></button>
          <div className="dropdown-menu">
            <li className="nav-item">
              <Link className="nav-link" to="/disinfector/distrib-materials"> </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/disinfector/mat-com-history"> </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/disinfector/mat-distrib-history"> </Link>
            </li>
          </div>
        </div>
      </div>
    );


    const operatorLinks = (
      <div className="operatorLinks">
        <div className="dropdown">
          <button type="button" className="btn btn-primary dropdown-toggle align-baseline mr-2" data-toggle="dropdown">.</button>
          <div className="dropdown-menu">
            <li className="nav-item">
              <Link className="nav-link" to="/operator/stats"></Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/fail/see">.  . </Link>
            </li>
          </div>
        </div>

        <div className="dropdown">
          <button type="button" className="btn btn-primary dropdown-toggle align-baseline mr-2" data-toggle="dropdown"></button>
          <div className="dropdown-menu">
            <li className="nav-item">
              <Link className="nav-link" to="/operator/order-queries"></Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/clients"></Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/operator/repeat-orders"> </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/not-completed-orders">. </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/search-orders"> </Link>
            </li>
            {/* {showRefreshButtton} */}
          </div>
        </div>

        <div className="dropdown">
          <button type="button" className="btn btn-primary dropdown-toggle align-baseline mr-2" data-toggle="dropdown"></button>
          <div className="dropdown-menu">
            <li className="nav-item">
              <Link className="nav-link" to="/create-order"></Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/add-client"></Link>
            </li>
          </div>
        </div>
      </div>
    );


    const accountantLinks = (
      <div className="accountantLinks">
        <div className="dropdown">
          <button type="button" className="btn btn-primary dropdown-toggle align-baseline mr-2" data-toggle="dropdown">.</button>
          <div className="dropdown-menu">
            <li className="nav-item">
              <Link className="nav-link" to="/accountant/stats"></Link>
            </li>
          </div>
        </div>

        <div className="dropdown">
          <button type="button" className="btn btn-primary dropdown-toggle align-baseline mr-2" data-toggle="dropdown"></button>
          <div className="dropdown-menu">
            <li className="nav-item">
              <Link className="nav-link" to="/accountant/queries"></Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/clients"></Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/search-orders"> </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/fail/see">.  . </Link>
            </li>
            {/* {showRefreshButtton} */}
          </div>
        </div>
      </div>
    );


    const subadminLinks = (
      <div className="subadminLinks">
        <div className="dropdown">
          <button type="button" className="btn btn-primary dropdown-toggle align-baseline mr-2" data-toggle="dropdown">.</button>
          <div className="dropdown-menu">
            <li className="nav-item">
              <Link className="nav-link" to="/subadmin/stats"> .</Link>
            </li>

            <li className="nav-item">
              <Link className="nav-link" to="/admin/stats"> .</Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/admin/disinf-stats">. </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/admin/operator-stats">.  / </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/admin/adv-stats">. </Link>
            </li>
          </div>
        </div>

        <div className="dropdown">
          <button type="button" className="btn btn-primary dropdown-toggle align-baseline mr-2" data-toggle="dropdown"></button>
          <div className="dropdown-menu">
            <li className="nav-item">
              <Link className="nav-link" to="/subadmin/orders"> </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/subadmin/queries"></Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/returned-queries"> </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/search-orders"> </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/clients"></Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/fail/see">.  . </Link>
            </li>
            {/* {showRefreshButtton} */}
          </div>
        </div>

        <div className="dropdown">
          <button type="button" className="btn btn-primary dropdown-toggle align-baseline mr-2" data-toggle="dropdown"></button>
          <div className="dropdown-menu">
            <li className="nav-item">
              <Link className="nav-link" to="/subadmin/material-coming-history"></Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/subadmin/material-distrib-history"></Link>
            </li>
          </div>
        </div>

        <div className="dropdown">
          <button type="button" className="btn btn-primary dropdown-toggle align-baseline mr-2" data-toggle="dropdown"></button>
          <div className="dropdown-menu">
            <li className="nav-item">
              <Link className="nav-link" to="/subadmin/materials"> </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/add-client"></Link>
            </li>
          </div>
        </div>
      </div>
    );


    const adminLinks = (
      <div className="adminLinks">

        <div className="dropdown">
          <button type="button" className="btn btn-primary dropdown-toggle align-baseline mr-2" data-toggle="dropdown">.</button>
          <div className="dropdown-menu">
            <li className="nav-item">
              <Link className="nav-link" to="/admin/stats"> </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/admin/disinf-stats"> </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/admin/operator-stats">.  / </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/admin/adv-stats"> </Link>
            </li>
          </div>
        </div>

        <div className="dropdown">
          <button type="button" className="btn btn-primary dropdown-toggle align-baseline mr-2" data-toggle="dropdown"></button>
          <div className="dropdown-menu">
            {/* <li className="nav-item">
              <Link className="nav-link" to="/admin/order-queries"></Link>
            </li> */}
            <li className="nav-item">
              <Link className="nav-link" to="/clients"></Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/fail/see">.  . </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/not-completed-orders">. </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/search-orders"> </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/admin/users"></Link>
            </li>
            {/* {showRefreshButtton} */}
          </div>
        </div>

        <div className="dropdown">
          <button type="button" className="btn btn-primary dropdown-toggle align-baseline mr-2" data-toggle="dropdown"></button>
          <div className="dropdown-menu">
            <li className="nav-item">
              <Link className="nav-link" to="/admin/material-coming-history"></Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/admin/material-history"></Link>
            </li>
          </div>
        </div>

        <div className="dropdown">
          <button type="button" className="btn btn-primary dropdown-toggle align-baseline mr-2" data-toggle="dropdown"></button>
          <div className="dropdown-menu">
            <li className="nav-item">
              <Link className="nav-link" to="/admin/material-coming"> </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/admin/materials"> </Link>
            </li>
            <li className="nav-item">
              {/* <Link className="nav-link" to="/admin/add-client"></Link> */}
              <Link className="nav-link" to="/add-client"></Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/register"> </Link>
            </li>
          </div>
        </div>
      </div>
    );


    return (
      <nav className="navbar navbar-expand-lg navbar-dark bg-primary pt-0 pb-0" id="navbar">
        <div className="container pt-2 pb-2 pl-0 pr-0">
          <Link to={`/${user.occupation}`} className="navbar-brand p-2">ProDez</Link>

          {this.props.options.theme === 'light' ? (
            <button className="navbar-brand" onClick={() => this.changeTheme('dark')}><i className="fas fa-moon"></i></button>
          ) : (
            <button className="navbar-brand" onClick={() => this.changeTheme('light')}><i className="fas fa-sun"></i></button>
          )}

          <button
            className="navbar-toggler"
            type="button"
            data-toggle="collapse"
            data-target="#navbarCollapse">
            <img src="../img/hamburger.svg" className="hamburger" alt="Hamburger" />
          </button>
          <div className="collapse navbar-collapse" id="navbarCollapse">
            <ul className="navbar-nav ml-auto list-inline">
              {user.occupation === 'admin' ? adminLinks : ('')}
              {user.occupation === 'subadmin' ? subadminLinks : ('')}
              {user.occupation === 'accountant' ? accountantLinks : ('')}
              {user.occupation === 'operator' ? operatorLinks : ('')}
              {user.occupation === 'disinfector' ? disinfectorLinks : ('')}
              {isAuthenticated ? authLinks : guestLinks}
            </ul>
          </div>
        </div>
      </nav>
    )
  }
}

const mapStateToProps = (state) => ({
  auth: state.auth,
  options: state.options
});

export default connect(mapStateToProps, { logoutUser, clearCurrentProfile, setTheme })(withRouter(Navbar));