import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import Moment from 'react-moment';

import materials from '../common/materials';
import removeZeros from '../../utils/removeZerosMat';
import calculateDisinfScore from '../../utils/calcDisinfScore';
import calculateStats from '../../utils/calcStats';
// import calcMaterialConsumption from '../../utils/calcMatConsumption';


class ShowDisStats extends Component {
  state = {
    //    ,   
    orders: this.props.admin.stats.orders,
    // ,   
    acceptedOrders: this.props.admin.stats.acceptedOrders,
    showOrders: false
  };




  renderOrders = (orders) => {

    return orders.map((order, key) => {
      // consumption array of specific confirmed order
      let consumptionArray = [];

      order.disinfectors.forEach(item => {
        consumptionArray.push({
          user: item.user,
          consumption: item.consumption
        });
      });

      let renderOrderConsumption = consumptionArray.map((object, number) =>
        <li key={number}>
          <p className="mb-0">: {object.user.occupation} {object.user.name}</p>
          {object.consumption.map((element, number) =>
            <p key={number} className="mb-0">{element.material}: {element.amount.toLocaleString()} {element.unit}</p>
          )}
        </li>
      );

      return (
        <div className="col-lg-4 col-md-6" key={key}>
          <div className="card order mt-2">
            <div className="card-body p-0">
              <ul className="font-bold mb-0 list-unstyled">
                {order.prevFailedOrder && <li><h4><i className="fas fas fa-exclamation"></i>   <i className="fas fas fa-exclamation"></i></h4></li>}

                {order.failed && <li><h4><i className="fas fas fa-exclamation"></i>   <i className="fas fas fa-exclamation"></i></h4></li>}

                {order.repeatedOrder ? (
                  <React.Fragment>
                    <li>  </li>
                    {order.repeatedOrderDecided ? (
                      <React.Fragment>
                        <li>     </li>
                        {order.repeatedOrderNeeded ? <li>  </li> : <li>   </li>}
                      </React.Fragment>
                    ) : <li>       </li>}
                  </React.Fragment>
                ) : ''}

                {order.completed ? <li> </li> : <li>   </li>}

                {order.operatorDecided ? (
                  <React.Fragment>
                    <li>  </li>
                    {order.operatorConfirmed ? (
                      <React.Fragment>
                        <li className="text-success">  (<Moment format="DD/MM/YYYY HH:mm">{order.operatorCheckedAt}</Moment>)</li>
                        <li> (0-5): {order.score}</li>
                        <li> : {order.clientReview ? order.clientReview : ' '}</li>
                      </React.Fragment>
                    ) : <li className="text-danger">  (<Moment format="DD/MM/YYYY HH:mm">{order.operatorCheckedAt}</Moment>)</li>}
                  </React.Fragment>
                ) : <li>    </li>}



                {order.accountantDecided ? (
                  <React.Fragment>
                    <li>  </li>
                    {order.accountantConfirmed ? (
                      <React.Fragment>
                        <li className="text-success">  (<Moment format="DD/MM/YYYY HH:mm">{order.accountantCheckedAt}</Moment>)</li>
                        <li>-: {order.invoice ? order.invoice : '--'}</li>
                        <li> : {order.cost.toLocaleString()} UZS (  {(order.cost / order.disinfectors.length).toLocaleString()} UZS)</li>
                      </React.Fragment>
                    ) : <li className="text-danger">  (<Moment format="DD/MM/YYYY HH:mm">{order.accountantCheckedAt}</Moment>)</li>}
                  </React.Fragment>
                ) : (
                  <React.Fragment>

                    {order.adminDecided ? (
                      <React.Fragment>
                        <li>  </li>
                        {order.adminConfirmed ? (
                          <li className="text-success">  (<Moment format="DD/MM/YYYY HH:mm">{order.adminCheckedAt}</Moment>)</li>
                        ) : <li className="text-danger">  (<Moment format="DD/MM/YYYY HH:mm">{order.adminCheckedAt}</Moment>)</li>}
                      </React.Fragment>
                    ) : (
                      <li>    </li>
                    )}

                  </React.Fragment>
                )}





                {/* {order.clientType === 'corporate' && order.paymentMethod === 'notCash' && !order.accountantDecided ? <li>    </li> : ''}

                {order.clientType === 'corporate' && order.paymentMethod === 'notCash' && order.accountantDecided ?
                  <React.Fragment>
                    <li>  </li>
                    {order.accountantConfirmed ? (
                      <React.Fragment>
                        <li className="text-success">  (<Moment format="DD/MM/YYYY HH:mm">{order.accountantCheckedAt}</Moment>)</li>
                        <li>-: {order.invoice}</li>
                        <li> : {order.cost.toLocaleString()} UZS (  {(order.cost / order.disinfectors.length).toLocaleString()} UZS)</li>
                      </React.Fragment>
                    ) : <li className="text-danger">  (<Moment format="DD/MM/YYYY HH:mm">{order.accountantCheckedAt}</Moment>)</li>}
                  </React.Fragment>
                  : ''}

                {order.clientType === 'corporate' && order.paymentMethod === 'cash' && !order.adminDecided ? <li>    </li> : ''}

                {order.clientType === 'corporate' && order.paymentMethod === 'cash' && order.adminDecided ? (
                  <React.Fragment>
                    <li>  </li>
                    {order.adminConfirmed ? (
                      <li className="text-success">  (<Moment format="DD/MM/YYYY HH:mm">{order.adminCheckedAt}</Moment>)</li>
                    ) : <li className="text-danger">  (<Moment format="DD/MM/YYYY HH:mm">{order.adminCheckedAt}</Moment>)</li>}
                  </React.Fragment>
                ) : ''}

                {order.clientType === 'individual' && !order.adminDecided ? <li>    </li> : ''}

                {order.clientType === 'individual' && order.adminDecided ? (
                  <React.Fragment>
                    <li>  </li>
                    {order.adminConfirmed ? (
                      <li className="text-success">  (<Moment format="DD/MM/YYYY HH:mm">{order.adminCheckedAt}</Moment>)</li>
                    ) : <li className="text-danger">  (<Moment format="DD/MM/YYYY HH:mm">{order.adminCheckedAt}</Moment>)</li>}
                  </React.Fragment>
                ) : ''} */}








                {order.clientType === 'corporate' ?
                  <React.Fragment>
                    {order.clientId ? (
                      <li className="text-danger"> : {order.clientId.name}</li>
                    ) : <li className="text-danger"> </li>}
                    <li className="text-danger"> : {order.client}</li>
                  </React.Fragment>
                  : ''}

                {order.clientType === 'individual' ?
                  <li className="text-danger"> : {order.client}</li>
                  : ''}

                <li className="text-danger"> : {order.phone}</li>
                {order.phone2 ? <li> : {order.phone2}</li> : ''}

                {order.completed ? (
                  <React.Fragment>
                    <li className="text-danger"> : <Moment format="DD/MM/YYYY">{order.dateFrom}</Moment></li>
                    <li className="text-danger"> : <Moment format="HH:mm">{order.dateFrom}</Moment></li>
                  </React.Fragment>
                ) : ''}

                {!order.completed && order.repeatedOrder && order.repeatedOrderDecided && order.repeatedOrderNeeded ? (
                  <React.Fragment>
                    <li className="text-danger"> : <Moment format="DD/MM/YYYY">{order.dateFrom}</Moment></li>
                    <li className="text-danger"> : <Moment format="HH:mm">{order.dateFrom}</Moment></li>
                  </React.Fragment>
                ) : ''}

                <li className="text-danger">: {order.address}</li>
                <li className="text-danger"> : {order.typeOfService}</li>

                <li> : {order.comment ? order.comment : '--'}</li>
                <li> : {order.disinfectorComment ? order.disinfectorComment : '--'}</li>
                <li>  ( ): {order.guarantee}</li>

                <li>  (  {order.disinfectors.length} ):</li>
                <ul className="font-bold mb-0">
                  {renderOrderConsumption}
                </ul>

                {order.completed && order.clientType === 'corporate' ? (
                  <React.Fragment>
                    {order.paymentMethod === 'cash' ? (
                      <React.Fragment>
                        <li> : </li>
                        <li> : {order.cost.toLocaleString()} UZS (  {(order.cost / order.disinfectors.length).toLocaleString()} UZS)</li>
                      </React.Fragment>
                    ) : (
                      <React.Fragment>
                        <li> : </li>
                        <li> : {order.contractNumber}</li>
                      </React.Fragment>
                    )}
                  </React.Fragment>
                ) : ''}


                {order.completed && order.clientType === 'individual' ?
                  <li> : {order.cost.toLocaleString()} UZS (  {(order.cost / order.disinfectors.length).toLocaleString()} UZS)</li>
                  : ''}

                {/* {order.completed ?
                  <Link to={`/order-full-details/${order._id}`} className="btn btn-primary"></Link>
                  :
                  <Link to={`/order-details/${order._id}`} className="btn btn-primary mr-1"></Link>
                } */}
              </ul>
            </div>
          </div>
        </div>
      );
    });
  };









  toggleShowOrders = (param) => {
    this.setState({
      showOrders: param
    });
  };

  render() {
    // calculate statistics
    let {
      // totalSum,
      totalScore,
      totalOrders,
      completed,
      confirmedOrders,
      rejected,
      consultAndOsmotrConfirmed,

      //     (    /  ,  )
      totalSumForDisinfector,

      failed,
      povtors,

      corporate,
      corporatePercent,
      corpSum,
      corpSumPercent,

      indiv,
      indivPercent,
      indivSum,
      indivSumPercent
    } = calculateStats(this.state.orders);

    let totalSumOfAcceptedOrders = 0;

    // let totalSum = 0,
    //   totalScore = 0,
    //   totalSumOfAcceptedOrders = 0,
    //   totalConsumption = [],
    //   completedOrders = [],
    //   confirmedOrders = [],
    //   rejectedOrders = [],
    //   failedOrders = 0;

    // let corporateClientOrders = {
    //   sum: 0,
    //   orders: 0
    // };

    // let indivClientOrders = {
    //   sum: 0,
    //   orders: 0
    // };


    // this.state.orders.forEach(order => {
    //   if (order.completed) {
    //     completedOrders.push(order);
    //   }

    //   if (order.clientType === 'corporate') {
    //     if (order.paymentMethod === 'cash') {
    //       if (order.operatorConfirmed && order.adminConfirmed) {
    //         confirmedOrders.push(order);
    //         totalSum += order.cost;
    //         totalScore += order.score;

    //         corporateClientOrders.orders++;
    //         corporateClientOrders.sum += order.cost;
    //       }

    //       if ((order.operatorDecided && !order.operatorConfirmed) || (order.adminDecided && !order.adminConfirmed)) {
    //         rejectedOrders.push(order);
    //       }
    //     }

    //     if (order.paymentMethod === 'notCash') {
    //       if (order.operatorConfirmed && order.accountantConfirmed) {
    //         confirmedOrders.push(order);
    //         totalSum += order.cost;
    //         totalScore += order.score;

    //         corporateClientOrders.orders++;
    //         corporateClientOrders.sum += order.cost;
    //       }
    //       if ((order.operatorDecided && !order.operatorConfirmed) || (order.accountantDecided && !order.accountantConfirmed)) {
    //         rejectedOrders.push(order);
    //       }
    //     }
    //   }




    //   if (order.clientType === 'individual') {
    //     if (order.completed && order.operatorConfirmed && order.adminConfirmed) {
    //       confirmedOrders.push(order);
    //       totalSum += order.cost / order.disinfectors.length;
    //       totalScore += order.score;

    //       indivClientOrders.orders++;
    //       indivClientOrders.sum += order.cost;
    //     }
    //     if (order.completed && ((order.operatorDecided && !order.operatorConfirmed) || (order.adminDecided && !order.adminConfirmed))) {
    //       rejectedOrders.push(order);
    //     }
    //   }

    //   if (order.failed) {
    //     failedOrders++;
    //   }

    //   // calculate total consumption of all orders of disinfector in given period
    //   if (order.completed) {

    //     order.disinfectors.forEach(element => {
    //       // we dont populate disinfectors.user field, this is why we write element.user === ..
    //       if (element.user._id === this.props.admin.stats.disinfectorId) {
    //         // if (element.user === this.props.admin.stats.disinfectorId) {
    //         element.consumption.forEach(object => {
    //           totalConsumption.forEach(item => {
    //             if (object.material === item.material && object.unit === item.unit) {
    //               item.amount += object.amount;
    //             }
    //           });
    //         });
    //       }
    //     });

    //   }

    // });



    // calculate total consumption of all orders of disinfector in given period

    // this function will not work because we are calculating consumption of specific user
    // let totalConsumption = calcMaterialConsumption(this.state.orders);

    let totalConsumption = [];
    materials.forEach(item => {
      const emptyObject = {
        material: item.material,
        amount: 0,
        unit: item.unit
      };
      totalConsumption.push(emptyObject);
    });

    //          ( )
    // ,        
    let approvedOrders = this.state.orders.filter(order =>
      order.completed &&
      !order.failed &&
      !order.hasOwnProperty('prevFailedOrder')
    );

    approvedOrders.forEach(order => {
      // this.state.orders.forEach(order => {

      if (order.completed) {

        order.disinfectors.forEach(element => {
          // we dont populate disinfectors.user field, this is why we write element.user === ..
          if (element.user._id === this.props.admin.stats.disinfectorId) {
            // if (element.user === this.props.admin.stats.disinfectorId) {
            element.consumption.forEach(object => {
              totalConsumption.forEach(item => {
                if (object.material === item.material && object.unit === item.unit) {
                  item.amount += object.amount;
                }
              });
            });
          }
        });

      }

    });


    this.state.acceptedOrders.forEach(order => {
      if (
        order.completed &&
        !order.failed &&
        order.operatorConfirmed &&
        (order.accountantConfirmed || order.adminConfirmed)
      ) {
        totalSumOfAcceptedOrders += order.cost;
      }

      // if (order.clientType === 'corporate') {
      //   if (order.paymentMethod === 'cash') {
      //     if (order.operatorConfirmed && order.adminConfirmed) {
      //       totalSumOfAcceptedOrders += order.cost;
      //     }
      //   }

      //   if (order.paymentMethod === 'notCash') {
      //     if (order.operatorConfirmed && order.accountantConfirmed) {
      //       totalSumOfAcceptedOrders += order.cost;
      //     }
      //   }
      // }

      // if (order.clientType === 'individual') {
      //   if (order.completed && order.operatorConfirmed && order.adminConfirmed) {
      //     totalSumOfAcceptedOrders += order.cost;
      //   }
      // }
    });

    // calculate average score
    let averageScore = calculateDisinfScore({
      totalScore: totalScore,
      totalOrders: confirmedOrders.length,
      failedOrders: failed
    }) || 0;

    // remove materials with amount of 0
    totalConsumption = removeZeros(totalConsumption);

    let renderTotalConsumption = totalConsumption.map((item, key) =>
      <li key={key}>{item.material}: {item.amount.toLocaleString()} {item.unit}</li>
    );


    let renderAllOrders = this.renderOrders(this.state.orders);

    let renderAcceptedOrders = this.renderOrders(this.state.acceptedOrders);

    return (
      <React.Fragment>
        <div className="row">
          <div className="col-lg-4 col-md-6">
            <div className="card order mt-2">
              <div className="card-body p-0">
                <h4 className="text-center"></h4>
                <ul className="font-bold mb-0 list-unstyled">
                  <li>  : {totalOrders}</li>
                  <li> : {completed}</li>
                  <li> : {confirmedOrders.length} (    : {consultAndOsmotrConfirmed})</li>

                  <li className="pt-2"> : {totalSumForDisinfector.toLocaleString()} UZS</li>
                  <li className="pb-2"> : {averageScore.toFixed(2)} ( 5)</li>
                  {/* <li> : {(totalScore / confirmedOrders.length).toFixed(2)} ( 5)</li> */}
                  <li> : {rejected}</li>
                  <li> : {failed}</li>
                  <li> : {povtors}</li>

                  <h6 className="mt-2">*            </h6>
                </ul>
              </div>
            </div>
          </div>

          <div className="col-lg-4 col-md-6">
            <div className="card order mt-2">
              <div className="card-body p-0">
                <ul className="font-bold mb-0 list-unstyled">
                  <li>  : {this.state.acceptedOrders.length}</li>
                  <li>   : {totalSumOfAcceptedOrders.toLocaleString()} UZS</li>

                  <h4 className="text-center"> </h4>
                  <li> : {corporate} ({corporatePercent} %)</li>
                  <li>  : {corpSum.toLocaleString()} UZS  ({corpSumPercent} %)</li>

                  <h4 className="text-center"> </h4>
                  <li> : {indiv} ({indivPercent} %)</li>
                  <li>  : {indivSum.toLocaleString()} UZS ({indivSumPercent} %)</li>
                </ul>
              </div>
            </div>
          </div>

          <div className="col-lg-4 col-md-6">
            <div className="card order mt-2">
              <div className="card-body p-0">
                <h4 className="text-center"> :</h4>
                <ul className="font-bold mb-0 list-unstyled">
                  {renderTotalConsumption}

                  <h6 className="mt-2">*       </h6>
                </ul>
              </div>
            </div>
          </div>
        </div>

        {this.state.showOrders ? (
          <React.Fragment>
            <div className="row mt-2">
              <div className="col-12">
                <button className="btn btn-dark" onClick={this.toggleShowOrders.bind(this, false)}><i className="fas fa-eye-slash"></i>  </button>
              </div>
            </div>

            <div className="row mt-2">
              <div className="col-12">
                <h2 className="text-center pl-3 pr-3">  </h2>
              </div>
              {this.state.orders.length > 0 ? (renderAllOrders) : <h2> </h2>}
            </div>

            <div className="row mt-2">
              <div className="col-12">
                <h2 className="text-center pl-3 pr-3">  </h2>
              </div>
              {this.state.acceptedOrders.length > 0 ? (renderAcceptedOrders) : <h2> </h2>}
            </div>
          </React.Fragment>
        ) : (
          <button className="btn btn-dark mt-2" onClick={this.toggleShowOrders.bind(this, true)}><i className="fas fa-eye"></i>  </button>
        )}


      </React.Fragment>
    )
  }
}

const mapStateToProps = (state) => ({
  auth: state.auth,
  order: state.order,
  admin: state.admin,
  errors: state.errors
});

export default connect(mapStateToProps)(withRouter(ShowDisStats));